import { styled } from "styled-components";
import OrangeBtn from "../common/OrangeBtn";
import { useState } from "react";
import axios from "axios";

const StyleInput = styled.input`
    width: 320px;
    height:44px;
    background-color: #fcfcfc;
    border: 1px solid #dee2e6;
    padding: 0 12px;
    font-size: 16px;
    &:focus{
        outline:none;
        border-color: #FF7A00;
        opacity: 0.8;
    }
`;
const ModalWrapper = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5); /*      */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index:1;
`;

const ModalContent = styled.div`
    display: flex;
    flex-direction: column;
    position: relative;
    min-height: 60vh;
    width: 50vw;
    background-color: white;
    align-items: center;
    border-radius:8px;
`;

const CloseButton = styled.button`
    position: absolute;
    top: 10px; 
    right: 10px;
    height: 10px;
    cursor: pointer;
    font-size: 24px;
    background: none;
    border: none;
    padding: 0;
    margin: 0;
    outline: none;
`;

const StyleSet = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 1vh;
`;

const RequestMatzipCard = ({ onClose }) => {
    const [channelName, setChannelName] = useState('');
    const [restaurantName, setRestuarantName] = useState('');
    const [videoUrl, setVideoUrl] = useState('');

    const onChangeChannelHandler = (e) => setChannelName(e.target.value);
    const onChangeRestuarantHandler = (e) => setRestuarantName(e.target.value);
    const onChangeVideoUrlHandler = (e) => setVideoUrl(e.target.value);

    const onClickRequest = async () => {
        const userId = sessionStorage.getItem("userId");
        let body = {
            userId: userId,
            channelName: channelName,
            restaurantName: restaurantName,
            videoUrl: videoUrl
        };
        if (!channelName || !restaurantName || !videoUrl) {
            alert("  .");
            return;
        }
        try {
            const response = await axios.post(`/requestBoard/requestRestaurant`, body, { withCredentials: true });
            switch (response.data) {
                case 0:
                    alert(" .");
                    onClose();
                    break;
                case 1:
                    alert("  .");
                    break;
                case 2:
                    alert(" .   ");
                    break;
                default:
                    alert("    .");
                    break;
            }
        } catch (error) {
            alert(' .  .');
            console.error(error);
            return false;
        };
    }

    return (
        <ModalWrapper>
            <ModalContent>
                <CloseButton onClick={onClose}>x</CloseButton>
                <h4 style={{ marginTop: "3vh", marginBottom: "0.4vh", fontWeight: "900" }}> </h4>
                <div style={{ fontSize: "0.8rem", color: "#717171" }}>    3~4  </div>
                <div style={{ fontSize: "0.8rem", color: "#717171" }}>     .</div>
                <div style={{ marginTop: "2vh", marginBottom: "3vh", height: "1px", width: "95%", backgroundColor: "#BBBBBB" }}> </div>
                <div>
                    <StyleSet>
                        <h3 style={{ backgroundColor: "white", color: "black" }}></h3>
                        <StyleInput value={channelName} onChange={onChangeChannelHandler} placeholder=" " type="text" />
                    </StyleSet>
                    <StyleSet>
                        <h3 style={{ backgroundColor: "white", color: "black" }}></h3>
                        <div style={{ marginLeft: "10px" }}>
                            <StyleInput value={restaurantName} onChange={onChangeRestuarantHandler} placeholder=" " type="text" />
                            <div style={{ fontSize: "11px", marginBottom: "1rem", marginTop: "0.1rem", color: "#FF7A00" }}>
                                    !
                            </div>
                        </div>
                    </StyleSet>
                    <StyleSet>
                        <h3 style={{ backgroundColor: "white", color: "black" }}> url</h3>
                        <div>
                            <StyleInput value={videoUrl} onChange={onChangeVideoUrlHandler} placeholder=" url " type="text" />
                            <div style={{ fontSize: "11px", marginBottom: "1rem", marginTop: "0.1rem", color: "#FF7A00" }}>
                                    url    !
                            </div>
                        </div>
                    </StyleSet>
                </div>
                <OrangeBtn text={" "} style={{ boxShadow: 'none', borderRadius: 0, marginTop: "3vh" }}
                    onClick={onClickRequest} />
            </ModalContent>
        </ModalWrapper>
    )
}

export default RequestMatzipCard;