import React, { useEffect, useState } from "react";
import { styled } from "styled-components";

const StyledInput = styled.input`
    width: 100%;
    color: red; /*    */
    -webkit-appearance: none; /*  CSS     */
    height: 15px; /*   */
    border: black 1px solid;
    border-radius: 5px; /*     */
    background: white; /*   */
    &::-webkit-slider-thumb {
            -webkit-appearance: none; /*  CSS     */
            width: 30px; /*    */
            height: 30px; /*    */
            border-radius: 50%; /*      */
            background: #FF7A00; /*    */
            cursor: pointer; /*       */
        }
`;

const ViewBar = ({ onViewChange }) => {
    const [value, setValue] = useState(10000);

    const handleChange = (event) => {
        setValue(event.target.value);
    };
    useEffect(() => {
        onViewChange(value);
    }, [value])

    return (
        <div>
            <StyledInput
                type="range"
                min={10000}
                max={1000000}
                step={10000} //    step   .
                value={value}
                onChange={handleChange}
            />
            <p> : {value >= 1000000 ? `${value / 10000}+` : `${value / 10000} `}</p>
        </div>
    );
};

export default ViewBar;
