import { Map, MarkerClusterer } from "react-kakao-maps-sdk"
import { useEffect, useRef, useState } from 'react';
import { useMarkers } from "../util/MyContext";
import axios from 'axios';
import OrangeBtn from '../common/OrangeBtn';
import '../../css/map.css'
import MapMarkerContainer from "./Markers";


const BasicMap = () => {
    const mapRef = useRef();
    const { markers, setMarkers, mapInfo, setMapInfo } = useMarkers();
    const [triggerEffect, setTriggerEffect] = useState(true);
    const config = {
        headers: {
            "Content-Type": "application/json", //  Content-Type  .
        },
        withCredentials: true,
    };

    //   ,     marker  
    const GetMapBound = async () => {
        const map = mapRef.current;
        const bounds = map.getBounds(); //   
        const swLatLng = bounds.getSouthWest(); // 
        const neLatLng = bounds.getNorthEast(); // 

        let x_start = swLatLng.getLng();
        let x_end = neLatLng.getLng();
        let y_start = swLatLng.getLat();
        let y_end = neLatLng.getLat();

        try {
            const response = await axios.get(`/home/search`, {
                params: {
                    x_start: x_start,
                    x_end: x_end,
                    y_start: y_start,
                    y_end: y_end,
                }
            }, config);
            setMarkers(response.data);
            setTriggerEffect(false);
        } catch (error) {
            alert(' .  .');
            console.error(error);
            return false;
        };
    }

    //markers     . , getMapBound  markers    triggerEffect 
    useEffect(() => {
        if (triggerEffect) {
            if (markers.length > 0) {
                const firstMarker = markers[0];
                // setMapInfo({
                //     lat: firstMarker.latlng.lat,
                //     lng: firstMarker.latlng.lng,
                //     level: 5,
                // });
            }
        }
        setTriggerEffect(true);
    }, [markers]);

    return (
        <div className={"mapContainer"} >
            <Map id="map" center={{ lat: mapInfo.lat, lng: mapInfo.lng }}
                style={{ width: "100%", height: "100%", }}
                level={mapInfo.level}
                ref={mapRef}
            >
                {/* <MarkerClusterer //        
                    averageCenter={true} //         
                    minLevel={8} //     
                >
                    {markers.map((marker, index) => (
                        <MapMarkerContainer
                            key={index}
                            marker={marker}
                            index={index}
                        />
                    ))};
                </MarkerClusterer> */}

                {markers.map((marker, index) => (
                    <MapMarkerContainer
                        key={index}
                        marker={marker}
                        index={index}
                    />
                ))};
            </Map>
            {console.log("markers", markers)};
            <div className={"RightDown"}>
                <OrangeBtn onClick={GetMapBound} text="  " />
            </div>
        </div >
    )
}

export default BasicMap;