import React, { useState, useEffect, useId } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import HeaderJoinBtn from './HeaderJoinBtn';
import HeaderLoginBtn from './HeaderLoginBtn';
import HeaderLogoutBtn from './HeaderLogoutBtn';
import styled from 'styled-components';
import '../../css/active.css'
import axios from 'axios';
import { useMarkers } from '../util/MyContext';

const HeaderContainer = styled.div`
    margin: 0;
    display: flex;
    padding-top: 10px;
    height: 50px;
    align-items: center;
`;

const Logo = styled.div`
    font-size: 25px;
    font-weight: 600;
    color: #FF7A00;
    margin-left: 20px;
    margin-right: 70px;
`;

const MenuItem = styled.div`
    margin-left: 20px;
    margin-right: 20px;
    font-size: 15px;
    font-weight: bold;
`;

const MyInfoItem = styled.div`
    position: absolute;
    right: 3%;
`;

const isNavActive = ({ isActive }) =>
    (isActive ? "active" : "inactive")
    ;

const Header = () => {
    const { isLogin, setIsLogin } = useMarkers();
    const userId = sessionStorage.getItem("userId");
    const navigate = useNavigate();

    // checkLoginStatus     
    const checkLoginStatus = async () => {
        try {
            const response = await axios.get(`/checkLoginStatus`, { withCredentials: true });
            console.log("checkLoginStatus:", response);
            //  isLoggedIn      isLogin  
            setIsLogin(response.data);
            console.log('Axios Is logged in:', response.data);
        } catch (error) {
            console.error('Error checking login status:', error);
        }
    };
    const HandleMyListClick = () => {
        if (!isLogin) {
            alert("   .");
            // <useNavigate to="/login" replace={true} />; // /list    
            navigate("/login", { replace: true }); // /list    
        }
    };
    //     
    useEffect(() => {
        console.log("header loaded");
        checkLoginStatus();
    }, []); //        

    return (
        <HeaderContainer>
            <Logo>Mat Zip</Logo>
            <MenuItem>
                <NavLink className={isNavActive} to="/">
                     
                </NavLink>
            </MenuItem>
            <MenuItem>
                {isLogin === false ? (
                    <NavLink className={isNavActive} to="/list"
                        onClick={() => {
                            if (!isLogin) {
                                alert("   .");
                                setTimeout(() => {
                                    navigate("/login", { replace: true });
                                }, 10);
                            }
                        }}>
                         Mat Zip
                    </NavLink>
                )
                    :
                    (
                        <NavLink className={isNavActive} to="/list">
                             Mat Zip
                        </NavLink>
                    )
                }
            </MenuItem>
            <MenuItem>
                <NavLink className={isNavActive} to="/request">
                    Mat Zip 
                </NavLink>
            </MenuItem>
            <MenuItem>
                <NavLink className={isNavActive} to="/question">
                    
                </NavLink>
            </MenuItem>
            {isLogin === false ? (
                <MyInfoItem>
                    <HeaderLoginBtn />
                    <HeaderJoinBtn />
                </MyInfoItem>
            )
                :
                (
                    <>
                        <span style={{ position: 'absolute', right: '30px' }}>
                            <span style={{ color: '#FF7A00' }}>{userId}</span> , 
                            <span style={{ fontWeight: 'bold' }}> Mat Zip</span>   !
                            <HeaderLogoutBtn />
                        </span>

                    </>

                )
            }

        </HeaderContainer>
    );
};

export default Header;
